.container {
    height: 100vh;
  }
  
  .container main {
    display: flex;
    justify-content: center;
    flex: 1;
    padding: 1rem;
  }
  
  .title {
    font-weight: 500;
    font-size: 1.5rem;
  }
  
    
  .logo {
    padding: 1rem 3rem;
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  
  .logo .image {
    display: flex;
    width: 3rem;
    height: 3rem;
  }
  
  .image img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  
 
  
  @media (max-width: 768px) {
    .logo {
      padding: 1rem;
      gap: 0.5rem;
    }
  
    .title {
      font-weight: 500;
      border: 2px solid red;
      font-size: 1.25rem;
    }
  
    .logo .image {
      display: flex;
      width: 2rem;
      height: 2rem;
    }
  }