.container {
    display: flex;
    flex-direction: column;
  }
  
  .input {
    display: flex;
    align-items: center;
    border: 2px solid #d0d0d0;
    padding: 0.5rem;
    border-radius: 0.5rem;
  }
  
  .input input {
    font-size: 1rem;
    padding: 0.5rem 1rem;
    border: none;
    flex: 1;
  }
  
  .input input:focus {
    outline: none;
  }
  
  .icon {
    display: flex;
    color: #828282;
  }
  
  .container p {
    margin-top: 0.25rem;
  }

  .error{
    color:red;
    font-size: 16px;
    letter-spacing: 0em;
    line-height: 24px;
  }