.container {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .input {
    display: flex;
    flex-direction: column;
    gap: 0.325rem;
  }
  
  .input input {
    width: 100%;
    border: 2px solid var(--neutral);
    padding: 0.75rem;
    border-radius: 0.75rem;
    font-size: 1rem;
  }
  
  .input input:focus {
    outline: none;
  }
  
  .radioGroup {
    flex: 1;
    display: flex;
    gap: 1.5rem;
    justify-content: space-between;
  }
  
  .radioOptions {
    display: flex;
    justify-content: flex-end;
    gap: 0.5rem;
    flex: 1;
  }
  
  .radioOption {
    padding: 0.5rem;
    border: 2px solid var(--neutral);
    border-radius: 0.5rem;
    font-size: 0.5rem;
    cursor: pointer;
    outline: none;
  }
  
  .checkedOption {
    background-color: var(--neutral);
  }

  .priority{
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 0.0025em;
    line-height: 16px;
  }
  
  .radioOption p {
    display: flex;
    align-items: center;
    gap: 0.25rem;
  }
  
  .radioOption > p span {
    font-size: 2rem;
  }
  
  .buttonGroup {
    display: flex;
  }
  
  .actions {
    display: flex;
    flex: 1;
    justify-content: flex-end;
    gap: 1rem;
  }
  
  .checklistHeading{
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0em;
    line-height: 24px;
  }
  
  .lists {
    max-height: 30vh;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    margin-top: 0.5rem;
  }
  
  .list {
    display: flex;
    gap: 1rem;
    padding: 0.75rem 1rem;
    border: 2px solid var(--neutral);
    border-radius: 0.75rem;
    align-items: center;
  }
  
  .list input[type='text'] {
    flex: 1;
    border: none;
    font-size: 1rem;
  }
  
  .list input[type='checkbox'] {
    width: 1rem;
    height: 1rem;
    accent-color: var(--primary);
  }
  
  .list input[type='text']:focus,
  .list input[type='text']:active {
    outline: none;
  }
  
  .list button {
    background-color: transparent;
    border: none;
    color: red;
    display: flex;
  }
  
  .addButton {
    margin-top: 0.5rem;
  }